@import "https://fonts.googleapis.com/css2?family=Roboto:wght@500&display=swap";
html {
  --hover-color: #00acdc;
}

body {
  margin: 0;
}

header {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.logo {
  width: 40px;
  height: 40px;
  display: block;
}

nav ul {
  text-transform: uppercase;
  justify-content: center;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

nav ul li + li {
  margin-left: 20px;
}

.nav__link {
  color: #000;
  text-align: center;
  align-items: center;
  height: 60px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 40px;
  text-decoration: none;
  display: flex;
  position: relative;
}

.is-active {
  color: var(--hover-color);
  position: relative;
}

.is-active:after {
  content: "";
  background-color: var(--hover-color);
  border-radius: 8px;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

.nav__link:hover {
  color: var(--hover-color);
}
